<template>
    <nav class="navbar navbar-default navbar-fixed-top" id="titleBar" style="padding:0;background-color:rgb(0,0,0);">
            <div class="navbar-header">
                <a class="navbar-brand" href="#">推比特 - 推开财富之门 </a>
                <button class="navbar-toggle collapsed" data-toggle="collapse" @click="showNav">
                  <span class="sr-only">Toggle navigation</span>
                  <span class="icon-bar"></span>
                  <span class="icon-bar"></span>
                  <span class="icon-bar"></span>
                </button>
            </div>
            <div class="navbar-collapse collapse" aria-expanded="true" style="" ref="navbar">
                <ul class="nav navbar-nav navbar-right" >
                    <li v-for="item in navItemList" :key="item.id">
                      <a href="javascript:void(0)" @click="goAnchor(item.href)"><strong> {{ item.name }}</strong> </a>
                    </li>
                </ul>
        </div>
    </nav>
</template>
<script>
/* eslint-disable */

export default {
  name: "HeaderNav",
  data() {
    return {
      items: [{ message: "Foo" }, { message: "Bar" }],
      navItemList: [
        {
          id: "001",
          name: "交易所",
          href: "#lm_jys"
        },
        {
          id: "002",
          name: "资讯",
          href: "#lm_zx"
        },
        {
          id: "003",
          name: "自媒体",
          href: "#lm_zmt"
        },
        {
          id: "004",
          name: "炒币工具",
          href: "#lm_cbgj"
        },
        {
          id: "005",
          name: "业界公司",
          href: "#lm_yjgs"
        },
        {
          id: "006",
          name: "书籍",
          href: "#lm_sj"
        },
        {
          id: "007",
          name: "杂货铺",
          href: "#lm_zhp"
        },
        {
          id: "008",
          name: "好文精选",
          href: "#lm_hwjx"
        }
      ]
    };
  },
  methods: {
    showNav() {
      const className = this.$refs.navbar.className;
      if (className.indexOf("in") > -1) {
        this.$refs.navbar.className = className.replace("in", "");
      } else {
        let classList = className.split(" ");
        classList.push("in");
        this.$refs.navbar.className = classList.join(" ");
      }
    },
    goAnchor(selector) {
      this.$emit("hashChange", selector);
    }
  }
};
</script>
<style lang="scss">
/*nomorlize*/
a {
  text-decoration: none;
}
ul,
li {
  margin: 0;
  list-style: none;
  padding-left: 0;
}
.navbar {
  &::before {
    content: " ";
    display: table;
  }
  &::after {
    clear: both;
    content: " ";
    display: table;
  }
  .collapse {
    display: none;
  }
  padding: 0;
  padding-left: 15px;
  padding-right: 15px;
  &.navbar-default {
    background-image: linear-gradient(#54b4eb, #2fa4e7 60%, #1d9ce5);
    background-repeat: no-repeat;
    border-bottom: 1px solid #178acc;
    -webkit-box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
  }
  &.navbar-fixed-top {
    top: 0;
    position: fixed;
    right: 0;
    left: 0;
    z-index: 1030;
    border-width: 0 0 1px;
  }

  @media (min-width: 768px) {
    .navbar-header {
      float: left;
    }
  }
  .navbar-header {
    &::before {
      content: " ";
      display: table;
    }
    &::after {
      clear: both;
      content: " ";
      display: table;
    }
    .navbar-brand {
      font-size: 24px;
      line-height: 18px;
      float: left;
      padding: 15px 15px;
    }
    @media (min-width: 768px) {
      .navbar-toggle {
        display: none;
      }
    }
    .navbar-toggle {
      position: relative;
      float: right;
      margin-right: 15px;
      padding: 9px 10px;
      margin-top: 8px;
      margin-bottom: 8px;
      background-color: transparent;
      background-image: none;
      border: 1px solid #178acc;
      border-radius: 4px;
      .sr-only {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        border: 0;
      }
      .icon-bar + .icon-bar {
        margin-top: 4px;
      }
      .icon-bar {
        display: block;
        width: 22px;
        height: 2px;
        border-radius: 1px;
        background-color: #ffffff;
      }
    }
  }

  @media (min-width: 768px) {
    .navbar-collapse.collapse {
      display: block !important;
      height: auto !important;
      padding-bottom: 0;
      overflow: visible !important;
      float: right !important;
    }
  }
  .navbar-collapse.collapse.in {
    display: block;
  }

  .navbar-nav {
    @media (min-width: 768px) {
      li {
        float: left;
      }
    }
    li {
      position: relative;
      display: block;
      a {
        &:hover {
          color: #ffffff;
          background-color: #178acc;
        }
        color: #ffffff;
        padding: 10px 15px;
        display: block;
        @media (min-width: 768px) {
          a {
            padding-top: 15px;
            padding-bottom: 15px;
          }
        }
      }
    }
  }
  @media (min-width: 768px) {
    .navbar-nav > li > a {
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }
}
</style>



