<template>
  <div
    class="vm-row"
    :style="style"
    :class="[
      justify !== 'start' ? 'is-justify-' + justify : '',
      align !== 'top' ? 'is-align-' + align : '',
      {
        'vm-row--flex': type === 'flex'
      }
    ]"
  >
    <slot></slot>
  </div>
</template>
<script>
  export default {
    name: 'VmRow',
    props: {
      gutter: Number,
      type: String,
      justify: {
        type: String,
        default: 'start'
      },
      align: {
        type: String,
        default: 'top'
      }
    },

    computed: {
      style() {
        var ret = {};
        // 定义左右偏移值
        if (this.gutter) {
          ret.marginLeft = `-${this.gutter / 2}px`;
          ret.marginRight = ret.marginLeft;
        }

        return ret;
      }
    }
  };
</script>
<style lang='scss'>
.vm-row {
        margin-bottom: 20px;
        &:last-child {
                margin-bottom: 0;
        }
}
.vm-row:before, .vm-row:after {
    display: table;
    content: "";
}
.vm-row:after {
    clear: both;
}
</style>



