import Vue from 'vue'
import Row from './row/row'
import Col from './col/col'
import './theme-default/src/index.scss'

// import Contianer from './Contianer/Contianer'

/* eslint-disable*/
// 注册所有component
const components = [
  Row,
  Col
]

const install = function (Vue, opt = {}) {
  if (install.installed) return
  components.map(component => {
    let capitalLetter = component.name.substr(0, 1).toLowerCase()
    let name = capitalLetter + component.name.substring(1).replace(/([A-Z])/g, '-$1').toLowerCase()
    Vue.component(name, component)
  })
}
install(Vue)

export default {
  version: '1.0.0',
  Row,
  Col
}



