<template>
    <div class="vm-board" :id="id">
        <div class="vm-board-title">{{title}}</div>
        <div v-if="category" class="vm-board-category">
            {{category}}
        </div>
        <vm-row >
            <vm-col  v-for="item in items" :key="item.id" :span="span" :xs="24" ><a :href="item.href" target="_blank">{{item.name}}</a></vm-col>
        </vm-row>
    </div>
</template>
<script>
/* eslint-disable */
export default {
  name: "Board",
  props: {
    id: String,
    title: String,
    category: String,
    items: {
      type: Array,
      required: true,
      default: function() {
        return [];
      }
    }
  },
  data() {
    return {};
  },
  computed: {
    span() {
      if (this.items.length >= 4) {
        return 6;
      }
      return 24 / this.items.length;
    }
  }
};
</script>

<style lang='scss'>
.vm-board {
  text-align: center;
  margin-top: 12px;
  .vm-col {
    a {
      color: rgb(88, 88, 88);
    }
  }
  .vm-board-title {
    font-size: 20px;
    line-height: 32px;
    font-weight: 600;
    color: #ffffff;
    background-color: #37a7e7;
  }
  .vm-board-category {
    line-height: 25px;
    background-color: #dddddd;
    padding-left: 10px;
    padding-top: 2px;
    margin-top: 7px;
    text-align: left;
  }
  .vm-col {
    background-color: #ffffff;
    border-radius: 5px;
    margin-top: 3px;
    height: 40px;
    font-size: 14px;
    line-height: 40px;
    border: 1px solid #e4ecf3;
  }
}
</style>



