<template>
<div
    class="vm-col" 
    :class ="classList"
    :style="style">
    <slot></slot>
</div>
</template>
<script>
export default {
  name: 'VmCol',

  props: {
    // 数组或对象
    span: {
      type: Number,
      default: 24
    },
    offset: Number,
    pull: Number,
    push: Number,
    xs: [Number, Object],
    sm: [Number, Object],
    md: [Number, Object],
    lg: [Number, Object]
  },

  computed: {
    gutter() {
      return this.$parent.gutter
    },

    style() {
      var ret = {}
      if (this.gutter) {
        ret.paddingLeft = this.gutter / 2 + 'px'
        ret.paddingRight = ret.paddingLeft
      }

      return ret
    },
    classList() {
      let classList = []
      let attribute = ['span', 'offset', 'pull', 'push']
      attribute.forEach(prop => {
        if (this[prop]) {
          classList.push(
            prop !== 'span'
              ? `vm-col-${prop}-${this[prop]}`
              : `vm-col-${this[prop]}`
          )
        }
      })
      // 针对各个设备做响应式布局
      let viewport = ['xs', 'sm', 'md', 'lg']
      viewport.forEach(size => {
        if (typeof this[size] === 'number') {
          classList.push(`vm-col-${size}-${this[size]}`)
        } else if (typeof this[size] === 'object') {
          let props = this[size]
          Object.keys(props).forEach(prop => {
            classList.push(
              prop !== 'span'
                ? `vm-col-${size}-${prop}-${props[prop]}`
                : `vm-col-${size}-${props[prop]}`
            )
          })
        }
      })
      return classList.join(' ')
    }
  }
}
</script>
<style lang='scss'>
@import '../theme-default/src/col.scss';
</style>


