<template>
  <div class="grid">
    <vm-row :gutter="20">
      <vm-col :span="24"><div class="grid-content bg-purple-dark"></div></vm-col>
    </vm-row>
    <vm-row>
      <vm-col :span="12"><div class="grid-content bg-purple"></div></vm-col>
      <vm-col :span="12"><div class="grid-content bg-purple-light"></div></vm-col>
    </vm-row>
    <vm-row>
      <vm-col :span="8"><div class="grid-content bg-purple"></div></vm-col>
      <vm-col :span="8"><div class="grid-content bg-purple-light"></div></vm-col>
      <vm-col :span="8"><div class="grid-content bg-purple"></div></vm-col>
    </vm-row>
    <vm-row>
      <vm-col :span="6"><div class="grid-content bg-purple"></div></vm-col>
      <vm-col :span="6"><div class="grid-content bg-purple-light"></div></vm-col>
      <vm-col :span="6"><div class="grid-content bg-purple"></div></vm-col>
      <vm-col :span="6"><div class="grid-content bg-purple-light"></div></vm-col>
    </vm-row>
    <vm-row>
      <vm-col :span="4"><div class="grid-content bg-purple"></div></vm-col>
      <vm-col :span="4"><div class="grid-content bg-purple-light"></div></vm-col>
      <vm-col :span="4"><div class="grid-content bg-purple"></div></vm-col>
      <vm-col :span="4"><div class="grid-content bg-purple-light"></div></vm-col>
      <vm-col :span="4"><div class="grid-content bg-purple"></div></vm-col>
      <vm-col :span="4"><div class="grid-content bg-purple-light"></div></vm-col>
    </vm-row>
  </div>
</template>

<script>
/* eslint-disable*/
export default {
  name: "Grid",
  data() {
    return {
      activeIndex2: "1",
      sliderValue: 32
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>
<style lang="scss">
.vm-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.vm-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>





