<template>
  <div id="app">
    <header-nav @hashChange="hashChange"></header-nav>
    <div class="container">
      <router-view/>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "App",
  methods: {
    hashChange(selector) {
      var anchor = this.$el.querySelector(selector);
      if (!anchor) {
        return;
      }
      document.documentElement.scrollTop = anchor.offsetTop - 50;
    }
  }
};
</script>

<style>
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
.container {
  margin-top: 60px;
  margin-right: auto;
  margin-left: auto;
}
</style>



